
.contact-animal-owner{
    background: #A03AF9;
    padding-top: 130px;
    padding-bottom: 130px;
    @media #{$mobile_device} {
       padding: 86px 0;
    }
    .contact_text{
        .section_title{
            h3{
                color: #fff;
                font-size: 38px;
                font-weight: 800;
                @media #{$sm}{
                    font-size: 38px;
                 }
                @media #{$xs}{
                    font-size: 38px;
                 }
            }
            p{
                font-size: 16px;
                color: #E5E5E5;
                line-height: 1.5;
                margin-top: 35px;
                margin-bottom: 45px;
            }

        }
        .contact_btn{
            @media #{$mobile_device} {
                display:  block !important;
            }
            p{
                margin-bottom: 0;
                font-size: 18px;
                font-weight: 400;
                // font-family: $font1;
                color: #fff;
                margin-left: 20px;
                @media #{$mobile_device} {
                    margin-left: 0;
                    margin-top: 30px;
                }
                a{
                    font-weight: 600;
                    color: #fff;
                    margin-left: 10px;
                }
            }
        }
    }
}

