.clients-area{
    .single-wrapper{
        padding-top: 100px;
        padding-bottom: 100px;
        padding-left: 80px;

        @media #{$xs}{
            padding-top: 70px;
            padding-bottom: 50px;
            
            padding-left: 20px;
        }

        position: relative;
        &::before{
            content: "";
            position: absolute;
            background: #ddd;
            width: 800%;
            height: 100%;
            top: 0;
            left: 0;
        }
    }
    .single-offers{
        padding-left: 60px;
        @media #{$sm}{
            padding-left: 0px;
        }
        @media #{$xs}{
            padding-left: 52px;
        }
        .offers-cap{
            position: relative;
            span{
                border: 1px solid transparent;
                position: absolute;
                content: "1";
                width: 35px;
                height: 35px;
                background: #F9AD3A;
                top: 7px;
                left: -61px;
                font-size: 14px;

                line-height: 35px;
                text-align: center;
                color: #fff;
                border-radius: 50%; 
                @include transition(.4s);
                @media #{$sm}{
                  display: none;
                }
            }
            h3{
              a{
                font-size: 30px;
                font-weight: 600;
                line-height: 1.5;
                margin-bottom: 16px;
                @include transition(.4s);
                @media #{$sm}{
                    font-size: 29px;
                }
                @media #{$xs}{
                    font-size: 22px;
                }
              }
            }
            p{
                color: #101A31;
                margin-bottom: 30px;
                @media #{$sm}{
                    font-size: 15px;
                }
            }
            a{
                i{
                    transform: rotate(45deg);
                    display: inline-block;
                    color: #fff;
                    font-size: 27px;
                    font-weight: 600;
                    @include transition(.4s);
                    
                }
            }
        }
    }
    .single-offers-img{
        img{
            width: 100%;
            @media #{$xs}{
                padding-top: 20px;
            }
        }
    }
}
// Hover Effect In single 
.single-offers:hover{
     .offers-cap{
         span{
             background:none;
             color: $theme-color;
             border: 1px solid $theme-color;
         }
         h3{
            a{
                color:$theme-color;
            }
         }
         a{
             i{
                 color: #fff;
             }
         }
     }
}