.visit-tailor-area{
    background: #ffff;
    display: flex;
    align-items: center;
    @media #{$md}{
      display: block;
    }
    @media #{$xs}{
      display: block;
    }
    @media #{$sm}{
      display: block;
    }
    // left Contents
    .tailor-details{
        width: 32%;
        padding: 20px 20px 20px 120px;
        @media #{$lg}{
            width: 53%;
            padding: 47px 20px 0px 50px;
        }
        @media #{$laptop}{
            width: 41%
        }
        @media #{$md}{
            width: 84%;
            padding: 50px 50px;
        }
        @media #{$sm}{
            width: 90%;
            padding: 51px 33px 20px 85px;
        }
        @media #{$xs}{
            width: 100%;
            padding: 51px 20px 20px 20px;
        }
        span{
            margin-bottom: 20px;
            display: block;
            color: #A03AF9;
            text-transform: uppercase;
            font-size: 14px;
            letter-spacing: 0.2em;
        }
        h2 {
            font-size: 40px;
            display: block;
            font-weight: 700;
            margin-bottom: 30px;
            @media #{$laptop}{
                font-size: 35px;
            }
            @media #{$lg}{
                font-size: 35px;
            }
            @media #{$sm}{
                font-size: 50px;
            }
            @media #{$xs}{
                font-size: 27px;
            }
        }
        p{
            font-size: 16px;
            color: #6E6575;
            line-height: 1.6;
            margin-bottom: 25px;
            font-weight: 500;
            @media #{$xs}{
                font-size: 15px;
            }
        }


    // Right mark
        .single-offers{
            padding-left: 60px;
            @media #{$sm}{
                padding-left: 0px;
            }
            @media #{$xs}{
                padding-left: 52px;
            }
            .offers-cap{
                position: relative;
                img{
                    position: absolute;
                    width: 35px;
                    height: 35px;
                    top: 7px;
                    left: -61px;
                    font-size: 14px;
                    text-align: center;
                    border-radius: 50%; 
                }
                h3{
                  a{
                    font-size: 30px;
                    font-weight: 600;
                    line-height: 1.5;
                    margin-bottom: 16px;
                    @include transition(.4s);
                    @media #{$sm}{
                        font-size: 29px;
                    }
                  }
                }
                p{
                    color: #101A31;
                    margin-bottom: 30px;
                    font-size: 16px;

                    @media #{$sm}{
                        font-size: 15px;
                    }
                }
                a{
                    i{
                        transform: rotate(45deg);
                        display: inline-block;
                        color: #fff;
                        font-size: 27px;
                        font-weight: 600;
                        @include transition(.4s);
                        
                    }
                }
            }
        }
        .single-offers-img{
            img{
                width: 100%;
                @media #{$xs}{
                    padding-top: 20px;
                }
            }
        }

        // Hover Effect In single 
        .single-offers:hover{
            .offers-cap{
                h3{
                    a{
                        color:#c6a16e;
                    }
                }
                a{
                    i{
                        color: #fff;
                    }
                }
            }
        }



         
    }
    // -Right Contents
    .tailor-offers{
        background-image: url(../img/gallery/visit_bg.png);
        background-size: 100%;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center center;
        height: 820px;
        width: 50%;
         @media #{$lg}{
            height: 700px;
            width: 61%;
        }
        @media #{$md}{
            width: 100%;
            height: 420px;
        }
        @media #{$sm}{
            width: 100%;
            height: 320px;

        }
        @media #{$xs}{
            width: 100%;
            height: 250px;
        }
    }
}

