.count-down-area{
    @media #{$md}{
        padding-bottom: 60px;
        padding-top: 60px;
    }
    @media #{$sm}{
        adding-bottom: 0;
        padding-top: 60px;
    }
    @media #{$xs}{
        padding-bottom: 0;
        padding-top: 50px;
    }
    .count-down-wrapper{
        background-repeat: no-repeat;
        background-size: cover;
        background-position: center center;
        .single-counter{
            margin-bottom: 10px;
            .counter{
                color: #1F0F2D;
                font-size: 40px;
                font-weight: 700;
                margin-bottom: 10px;
                display: inline-block;
                line-height: 1;
            }
            p{
                color: #1F0F2D;
                font-size: 40px;
                font-weight: 700;
                margin-bottom: 10px;
                display: inline-block;
                line-height: 1;

            }
        }
        .pera-count p {
            color: #F9AD3A !important   ;
            font-size: 16px;
        }
    }
}


.count-btn{
    text-decoration: underline ;
    color: $theme-color;
    &:hover{
        color: $theme-color;
        text-decoration: underline ;
        letter-spacing: 0.5px;
    }
}