.founder-img {
	margin-left: -15px;
}


.testimonial-area{
    position: relative;
    height: 500px;
    width: 100%;

    .about-caption{
        background: #fff;
        // position: relative;
        // bottom: -80px;
    }
    .testimonial-caption{
        position: relative;
         img {
            position: absolute;
            top: -27px;
            left: -44px;
            z-index: -1;
            @media #{$xs}{
                display: none;
            }
         }
        p{
            font-size: 19px;
            color: #540000;
            line-height: 1.6;
            margin-bottom: 46px;

            @media #{$lg}{
                margin-bottom: 40px;
            }
            @media #{$sm}{
                width: 99%;
            }
            @media #{$xs}{
                margin-bottom: 33px;
            }
        }
    }
    .single-testimonial{
        padding:85px 65px 0px 91px;
        @media #{$md}{
            padding: 119px 10px 18px 86px;
        }
        @media #{$sm}{
            padding: 89px 10px 18px 57px
        }
        @media #{$xs}{
            padding: 54px 16px 2px 18px;
        }
    }
    .rattiong-caption{
        span{
            color: #540000;
            font-size: 24px;
            font-weight: 500;
            text-transform: uppercase;
           font-family:$font_1;
            span{
                color: #540000;
                font-size: 16px;
                text-transform: capitalize;
                padding-left: 15px;
            }
        }
    }
    .founder-text {
        // margin-left: 24px;
        span{

            color: #540000;
            font-weight: 500;
            line-height: 1.5;
            font-size: 20px;
            font-family: $font_1;
            margin-bottom: 3px;
            display: block;

            @media #{$xs}{
                font-size: 20px;
            }
        }
        p {
            color: #540000;
            font-size: 14px;
            @media #{$xs}{
                font-size: 12px;
            }
        }
    }
}


/* Homepage1 Testimonial */
.h1-testimonial-active{
    button.slick-arrow {
        top: 52%;
        right: 13%;

        position: absolute;
        border: 0;
        padding: 10px;
        cursor: pointer;
        background: none;
        @include transition(.4s);
        right: 90px;
        margin: 0 10px;
        font-size: 20px;
        color: rgba(168, 0, 0,0.3);
        bottom: 0px;
        z-index: 1;
        transform: rotate(-45deg) !important;
        &:hover{
            color:#A80000;
        }
    } 

   button.slick-next {
        // left: 55px !important;
        right: 27px;
        transform: rotate(45deg) !important;
    }

    // slick-dot style
    &.dot-style{
