// End dot style
}


.slick-initialized .slick-slide {
    outline: 0;
}



.testimonial-padding{
    padding-top: 130px;
    padding-bottom: 0px;
    @media #{$lg}{
        padding-top: 130px;
        padding-bottom: 0px;
    }
    @media #{$md}{
        padding-top: 130px;
        padding-bottom: 0px;
    }
    @media #{$sm}{
        padding-top: 100px;
        padding-bottom: 0px;
    }
    @media #{$xs}{
        padding-top: 100px;
        padding-bottom: 0px;
    }
}